.custom-table .ant-table table {
  border-radius: 0 !important; }

.custom-table .ant-table-thead {
  border-radius: 0px; }
  .custom-table .ant-table-thead th {
    border-right: none !important;
    border-left: none !important;
    background: #208ccc; }

.custom-table .ant-table-fixed-right table {
  border-left: none; }

.custom-table .ant-table-column-title {
  color: #fff;
  font-weight: bold; }
  .custom-table .ant-table-column-title .title-box {
    background: #208ccc;
    padding: 0 16px;
    min-height: 45px;
    display: flex;
    align-items: center; }
  .custom-table .ant-table-column-title .addition-box {
    background-color: #ffffff;
    padding: 10px;
    min-height: 55px; }
    .custom-table .ant-table-column-title .addition-box .ant-select {
      width: 100%; }

.custom-table .ant-table-header .ant-table-thead .ant-table-header-column {
  width: 100%; }

.custom-table .ant-table-hide-scrollbar {
  display: flex; }
  .custom-table .ant-table-hide-scrollbar .ant-table-fixed {
    flex: 1; }

.custom-table.custom .ant-table-header .ant-table-thead th {
  padding: 0; }

.custom-table.custom .button-column-action {
  width: 30px;
  height: 30px;
  border-radius: 15px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center; }
  .custom-table.custom .button-column-action .btn-action {
    width: 3px; }

.custom-table.custom .button-column-action:hover {
  background: #26a9e0; }

.custom-table.custom .col-action {
  display: flex; }
  .custom-table.custom .col-action .btn {
    color: #fff;
    margin: 2px;
    cursor: pointer; }

.custom-table.custom .ant-table-scroll table .ant-table-fixed-columns-in-body:not([colspan]) > * {
  visibility: initial; }

.custom-table.custom .ant-table-fixed-right {
  border-radius: inherit; }

.panel-container .custom-table {
  margin-left: -16px !important;
  margin-right: -16px !important; }
