#__next {
  width: 100%;
  height: 100%; }

.app > div:first-child {
  width: 100%;
  height: 100%; }

body,
div,
p,
span,
label,
b,
input,
button,
strong {
  font-family: Muli, "Source Sans Pro" !important; }

::-webkit-scrollbar-thumb {
  background: #2196f3;
  border-radius: 10px; }

::-webkit-scrollbar {
  width: 7px;
  height: 7px; }

.search-box {
  background: #ffffff;
  border: 1px solid #dfdfdf;
  box-sizing: border-box;
  border-radius: 100px;
  height: 32px;
  display: flex;
  align-items: center;
  padding: 0 10px; }
  .search-box img {
    height: 15px; }
  .search-box input {
    border: none;
    outline: none;
    margin-left: 10px;
    font-size: 14px;
    color: #000;
    flex: 1;
    width: 100%; }

.footer {
  display: flex;
  margin-top: 10px; }

.panel-hdr h2 {
  font-size: 18px;
  text-transform: uppercase; }
