/**
CAROUSEL
 */
.carousel-control-prev-icon, .carousel-control-next-icon {
  width: 24px;
  height: 24px;
  background-color: #386CB6;
  background-repeat: no-repeat;
  background-size: 50% 50%;
  border-radius: 12px;
  cursor: pointer; }

.carousel-control-prev {
  left: -41px;
  width: auto;
  opacity: 1; }

.carousel-control-next {
  right: -41px;
  width: auto;
  opacity: 1; }

/**
END CAROUSEL
 */
/**
BUTTON
 */
.btn-primary, .btn-primary:hover {
  background-color: #386CB6;
  border-color: #386CB6;
  border-radius: 16px; }

.btn-success, .btn-success:hover {
  background-color: #00D2B1;
  border-color: #00D2B1;
  border-radius: 16px; }

.navbar-toggler {
  border: none;
  outline: none !important; }

/**
END BUTTON
 */
/**
MISC
 */
.text-primary {
  color: #00D2B1 !important; }

.text-secondary {
  color: #386CB6 !important; }

body {
  width: 100%;
  height: 100%; }

.carousel-indicators {
  bottom: 40px; }
